import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PieChart, {
    Legend,
    Export,
    Series,
    Label,
    Font,
    Connector,
} from 'devextreme-react/pie-chart';
import { NUTRITION_HISTORY_REQUEST } from '../../../store/modules/myPage';
import {
    MessageWrapper,
    LackMessage,
    NormalMessage,
    TooMuchMessage,
    Title,
    DatePickerWrapper,
    EmptyText,
    Description,
    editBox,
    modalTitle,
    modalBody,
    CancelButton,
    ButtonWrapper,
} from '../MyPage.style';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

function customizeText(arg) {
    // return `${arg.argument} ${arg.valueText} (${arg.percentText})`;
    return `${arg.valueText} (${arg.percentText})`;
}



export default function NutritionHistory() {
    const dispatch = useDispatch();
    const { nutritionHistoryList } = useSelector(state => state.mypage);
    const { me } = useSelector(state => state.mypage);
    const [ startDate, setStartDate ] = useState(new Date());

    const CarbohydrateList = [];
    const ProteinList = [];
    const FatList = [];
    const dataSource = [];

    var totalCarbohydrate = 0;
    var totalProtein = 0;
    var totalFat = 0;

    if(nutritionHistoryList.length !== 0) {
        for(let i=0; i<nutritionHistoryList.length; i++) {
            CarbohydrateList.push(nutritionHistoryList[i].carbohydrate);
            ProteinList.push(nutritionHistoryList[i].protein);
            FatList.push(nutritionHistoryList[i].fat);
        }
        totalCarbohydrate = CarbohydrateList.reduce((sum, currValue) => {
            return sum + currValue;
        });
        totalProtein = ProteinList.reduce((sum, currValue) => {
            return sum + currValue;
        });
        totalFat = FatList.reduce((sum, currValue) => {
            return sum + currValue;
        });

        dataSource.push({ nutrition: '', amount: totalCarbohydrate.toFixed(2) },)
        dataSource.push({ nutrition: '', amount: totalProtein.toFixed(2) },)
        dataSource.push({ nutrition: '', amount: totalFat.toFixed(2) },)
    }

    const now = new Date();   //    
    const year = now.getFullYear(); // 
    const month = now.getMonth();   // 
    const day = now.getDate();      // 
    var userYear = '';
    var userMonth = '';
    var userDay = '';
    if(me.birthday !== null) {
        userYear = me.birthday.substring(0, 4);
        userMonth = me.birthday.substring(5, 7);
        userDay = me.birthday.substring(9, 10);
    }

    var age = 0;

    if(month < userMonth) {
        age = year - userYear - 1;
    } else if(month === userYear) {
        if(day < userDay) {
        age = year - userYear - 1;
        }
    }

    //    (Mifflin-St Jeor Equation)
    var bmr = 0;
    if(!me.gender) { //  
        bmr = (me.weight * 10 + me.height * 6.25 - 5 * age + 5);
    } else {         //  
        bmr = (me.weight * 10 + me.height * 6.25 - 5 * age - 161);
    }

    //    4:4:2  5:2:3 . 
    //   ,
    //  : bmr * 0.5 / 4
    //  : bmr * 0.2 / 4
    //  : bmr * 0.3 / 9

    var carbohydrateCheck = 0;
    var proteinCheck = 0;
    var fatCheck = 0;
    //  ( : 45% ~ 65%)
    if(totalCarbohydrate < (bmr * 0.45) / 4) {    // 
        carbohydrateCheck = 0;
    } else if(totalCarbohydrate >= ((bmr * 0.45) / 4) && totalCarbohydrate <= ((bmr * 0.65) / 4)) {     // 
        carbohydrateCheck = 1;
    } else {
        carbohydrateCheck = 2;
    }

    //  ( : 10% ~ 35%)
    if(totalProtein < (bmr * 0.1) / 4) {
        proteinCheck = 0;
    } else if(totalProtein >= ((bmr * 0.1) / 4) && totalProtein <= ((bmr * 0.35) / 4)) {
        proteinCheck = 1;
    } else {
        proteinCheck = 2;
    }

    //  ( : 20% ~ 35%)
    if(totalFat < (bmr * 0.2) / 9) {
        fatCheck = 0;
    } else if(totalFat >= ((bmr * 0.2) / 9) && totalFat <= ((bmr * 0.35) / 9)) {
        fatCheck = 1;
    } else {
        fatCheck = 2;
    }

    //  
    const [ open, setOpen ] = useState(false);
    const [ title, setTitle ] = useState("");
    const [ message, setMessage ] = useState("");
    const handleOpen = (status, type) => {
        if(status === 'lack') {
            if(type === 'carbohydrate') {
                setTitle(" ");
                setMessage("   .    ,     . ");
            } else if(type === 'protein') {
                setTitle(" ");
                setMessage("   .    ,     . ");
            } else {
                setTitle(" ");
                setMessage("   .    ,     . ");
            }
        } else if(status === 'normal') {
            setTitle(" ");
            setMessage(' .     ! ');
        } else {
            setTitle(" ");
            setMessage(' .    ! ');
        }
        setOpen(true);
    }
    const handleClose = () => setOpen(false);

    function dateFormat(date) {
        let month = date.getMonth() + 1;
        let day = date.getDate();

        month = month >= 10 ? month : '0' + month;
        day = day >= 10 ? day : '0' + day;

        return date.getFullYear() + '-' + month + '-' + day;
    }

    useEffect(() => {
        const createdAt = dateFormat(startDate);
        dispatch({
            type: NUTRITION_HISTORY_REQUEST,
            data: {
                createdAt: createdAt,
            }
        });
    }, [ startDate ]);

    return (
        <div style={{ height: '100%', width: '100%'}}>
            <Title>  </Title>
            <DatePickerWrapper>
                <DatePicker 
                    dateFormat="yyyy-MM-dd"    //   
                    wrapperClassName='w-full'
                    selected={startDate} 
                    onChange={
                        (date) => setStartDate(date)
                    } />
            </DatePickerWrapper>
            <Description>*   .</Description>
            {
                nutritionHistoryList.length === 0 && 
                <>
                    <EmptyText>  <br/> .</EmptyText>
                </>
            }
            {
                nutritionHistoryList.length !==0 &&
                <>
                <MessageWrapper>
                    { carbohydrateCheck === 0 && <LackMessage onClick={ () => handleOpen('lack', 'carbohydrate') }></LackMessage> }
                    { carbohydrateCheck === 1 && <NormalMessage onClick={ () => handleOpen('normal', 'carbohydrate') }></NormalMessage> }
                    { carbohydrateCheck === 2 && <TooMuchMessage onClick={ () => handleOpen('toomuch', 'carbohydrate') }></TooMuchMessage> }
                    { proteinCheck === 0 && <LackMessage onClick={ () => handleOpen('lack', 'protein') }></LackMessage> }
                    { proteinCheck === 1 && <NormalMessage onClick={ () => handleOpen('normal', 'protein') }></NormalMessage> }
                    { proteinCheck === 2 && <TooMuchMessage onClick={ () => handleOpen('toomuch', 'protein') }></TooMuchMessage> }
                    { fatCheck === 0 && <LackMessage onClick={ () => handleOpen('lack', 'fat') }></LackMessage> }
                    { fatCheck === 1 && <NormalMessage onClick={ () => handleOpen('normal', 'fat') }></NormalMessage> }
                    { fatCheck === 2 && <TooMuchMessage onClick={ () => handleOpen('toomuch', 'fat') }></TooMuchMessage> }
                </MessageWrapper>
                <Description>
                    *    ,<br/>
                     ,  ,   .
                </Description>
                <PieChart
                    id='pie'
                    palette='Material'
                    dataSource={dataSource}
                    >
                    <Legend
                        orientation='horizontal'
                        itemTextPosition='right'
                        horizontalAlignment='center'
                        verticalAlignment='bottom'
                        columnCount={4}/>
                    <Export enabled={true}/>
                    <Series argumentField='nutrition' valueField='amount'>
                        <Label visible={true} position='columns' customizeText={customizeText}>
                            <Font size={16}/>
                            <Connector visible={true} width={0.5}/>
                        </Label>
                    </Series>
                </PieChart>
                </>
            }
        <Modal
            open={ open }
            onClose={ handleClose }
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Box sx={ editBox }>
            <Typography id="modal-modal-title" style={ modalTitle }>
                { title }
            </Typography>
            <hr/>
            <Typography id="modal-modal-description" stlye={ modalBody }>
                { message }
            </Typography>
            {/* <hr/> */}
            <ButtonWrapper>
                <CancelButton onClick={ handleClose }>
                    
                </CancelButton>
            </ButtonWrapper>
            </Box>
        </Modal>
        </div>
    );
}