  try {
    const result = yield call(myDietUpdateAPI, action.data);
    yield put({ type: MY_DIET_UPDATE_SUCCESS, data: result });
    swal("  ", "  ", "success", {
      buttons: false,
      timer: 1800,
    });
  } catch (error) {
    yield put({ type: MY_DIET_UPDATE_FAILURE });
  }
